/**
 * ÿ£ÿØŸàÿßÿ™ ÿ™ÿ≠ÿ≥ŸäŸÜ ÿ±Ÿàÿßÿ®ÿ∑ Contentful + ÿ≠ŸÑ ŸÖÿ≥ÿßÿ±ÿßÿ™ ÿßŸÑŸàÿ≥ÿßÿ¶ÿ∑ (directAccess)
 * - tuneContentful*: ŸÑÿ™ÿ≠ÿ≥ŸäŸÜ ÿßŸÑÿµŸàÿ± ŸÖŸÜ Contentful
 * - removeContentfulProxy: ŸÑÿ•ÿ±ÿ¨ÿßÿπ ÿ±ÿßÿ®ÿ∑ Contentful ÿßŸÑÿ£ÿµŸÑŸä ÿ•ŸÜ ŸÉÿßŸÜ ŸäŸÖÿ± ÿπÿ®ÿ± /api/proxy/file
 * - directAccess: ŸäŸÇÿ±Ÿëÿ± ÿ•ŸÜ ŸÉÿßŸÜ ÿßŸÑÿ±ÿßÿ®ÿ∑ Ÿäÿ¨ÿ® ÿ£ŸÜ ŸäŸèÿ≠ŸÖŸëŸéŸÑ ŸÖÿ®ÿßÿ¥ÿ±ÿ© ÿ£Ÿà ÿπÿ®ÿ± ÿßŸÑÿ®ÿ±ŸàŸÉÿ≥Ÿä
 */

/* ===================== ÿ™ÿ≠ÿ≥ŸäŸÜÿßÿ™ Contentful ŸÑŸÑÿµŸàÿ± ===================== */

/**
 * ÿ™ÿ≠ÿ≥ŸäŸÜ ÿ±ÿßÿ®ÿ∑ Contentful ŸÑÿ™ŸÇŸÑŸäŸÑ ÿßŸÑÿ≠ÿ¨ŸÖ Ÿàÿ™ÿ≠ÿ≥ŸäŸÜ ÿßŸÑÿ£ÿØÿßÿ°
 * @param url - ÿßŸÑÿ±ÿßÿ®ÿ∑ ÿßŸÑÿ£ÿµŸÑŸä ŸÖŸÜ Contentful
 * @param options - ÿÆŸäÿßÿ±ÿßÿ™ ÿßŸÑÿ™ÿ≠ÿ≥ŸäŸÜ
 * @returns ÿßŸÑÿ±ÿßÿ®ÿ∑ ÿßŸÑŸÖÿ≠ÿ≥ŸÜ
 */
export function tuneContentful(
  url: string,
  options: {
    quality?: number;
    width?: number;
    format?: 'webp' | 'avif' | 'jpg' | 'png';
    removeHeight?: boolean;
  } = {}
): string {
  try {
    const u = new URL(url);

    // ÿ™ÿ≠ŸÇŸÇ ŸÖŸÜ ÿ£ŸÜ ÿßŸÑÿ±ÿßÿ®ÿ∑ ŸÖŸÜ Contentful
    if (!u.hostname.endsWith('ctfassets.net')) {
      return url;
    }

    const {
      quality = 70,
      width = 1600,
      format = 'webp',
      removeHeight = true,
    } = options;

    // ÿ™ÿ∑ÿ®ŸäŸÇ ÿßŸÑÿ™ÿ≠ÿ≥ŸäŸÜÿßÿ™
    u.searchParams.set('fm', format);
    u.searchParams.set('q', quality.toString());

    // ÿ™ÿπÿØŸäŸÑ ÿßŸÑÿπÿ±ÿ∂ ÿ•ÿ∞ÿß ŸÉÿßŸÜ ŸÖŸàÿ¨ŸàÿØÿßŸã
    if (u.searchParams.has('w')) {
      u.searchParams.set('w', width.toString());
    }

    // ÿ•ÿ≤ÿßŸÑÿ© ÿßŸÑÿßÿ±ÿ™ŸÅÿßÿπ ŸÑŸÑÿ≥ŸÖÿßÿ≠ ŸÑŸÄ Next/Image ÿ®ÿ∂ÿ®ÿ∑Ÿá ÿ™ŸÑŸÇÿßÿ¶ŸäÿßŸã
    if (removeHeight && u.searchParams.has('h')) {
      u.searchParams.delete('h');
    }

    return u.toString();
  } catch {
    // ŸÅŸä ÿ≠ÿßŸÑÿ© ÿÆÿ∑ÿ£ ŸÅŸä ÿ™ÿ≠ŸÑŸäŸÑ ÿßŸÑÿ±ÿßÿ®ÿ∑ÿå ÿ£ÿ±ÿ¨ÿπ ÿßŸÑÿ±ÿßÿ®ÿ∑ ÿßŸÑÿ£ÿµŸÑŸä
    return url;
  }
}

/** ÿ™ÿ≠ÿ≥ŸäŸÜ ÿ±ÿßÿ®ÿ∑ ŸÑŸÑÿµŸàÿ± ŸÅŸä ÿßŸÑŸÖÿπÿßÿ±ÿ∂ (ÿ¨ŸàÿØÿ© ÿ£ŸÇŸÑ ŸÑŸÑÿ™ÿ≠ŸÖŸäŸÑ ÿßŸÑÿ≥ÿ±Ÿäÿπ) */
export function tuneContentfulForGallery(url: string): string {
  return tuneContentful(url, {
    quality: 60,
    width: 1200,
    format: 'webp',
  });
}

/** ÿ™ÿ≠ÿ≥ŸäŸÜ ÿ±ÿßÿ®ÿ∑ ŸÑŸÑÿµŸàÿ± ÿßŸÑŸÖÿµÿ∫ÿ±ÿ© */
export function tuneContentfulForThumbnail(url: string): string {
  return tuneContentful(url, {
    quality: 65,
    width: 400,
    format: 'webp',
  });
}

/** ÿ™ÿ≠ÿ≥ŸäŸÜ ÿ±ÿßÿ®ÿ∑ ŸÑŸÑÿµŸàÿ± ÿπÿßŸÑŸäÿ© ÿßŸÑÿ¨ŸàÿØÿ© (Hero images) */
export function tuneContentfulForHero(url: string): string {
  return tuneContentful(url, {
    quality: 80,
    width: 1920,
    format: 'webp',
  });
}

/**
 * ÿ•ÿ≤ÿßŸÑÿ© ÿßŸÑÿ®ÿ±ŸàŸÉÿ≥Ÿä ŸÖŸÜ ÿ±ÿßÿ®ÿ∑ Contentful ÿ•ÿ∞ÿß ŸÉÿßŸÜ ŸäŸÖÿ± ÿπÿ®ÿ± /api/proxy/file
 */
export function removeContentfulProxy(url: string): string {
  try {
    const u = new URL(url);

    // ÿ™ÿ≠ŸÇŸÇ ŸÖŸÜ ÿ£ŸÜ ÿßŸÑÿ±ÿßÿ®ÿ∑ ŸäŸÖÿ± ÿπÿ®ÿ± ÿßŸÑÿ®ÿ±ŸàŸÉÿ≥Ÿä
    if (u.pathname === '/api/proxy/file' && u.searchParams.has('url')) {
      const originalUrl = u.searchParams.get('url');
      if (originalUrl) {
        const decodedUrl = decodeURIComponent(originalUrl);
        const originalU = new URL(decodedUrl);

        // ÿ™ÿ≠ŸÇŸÇ ŸÖŸÜ ÿ£ŸÜ ÿßŸÑÿ±ÿßÿ®ÿ∑ ÿßŸÑÿ£ÿµŸÑŸä ŸÖŸÜ Contentful
        if (originalU.hostname.endsWith('ctfassets.net')) {
          return decodedUrl;
        }
      }
    }

    return url;
  } catch {
    return url;
  }
}

/* ===================== ÿ≠ŸÑ ŸÖÿ≥ÿßÿ±ÿßÿ™ ÿßŸÑŸàÿ≥ÿßÿ¶ÿ∑ (ÿ®ÿ±ŸàŸÉÿ≥Ÿä/ÿ™ÿ≠ŸÖŸäŸÑ ŸÖÿ®ÿßÿ¥ÿ±) ===================== */

/** ÿ™ÿ∑ÿßÿ®ŸÇ ÿ¢ŸÖŸÜ: Ÿäÿ∑ÿßÿ®ŸÇ ÿßŸÑÿØŸàŸÖŸäŸÜ ŸÜŸÅÿ≥Ÿá ÿ£Ÿà ÿ£Ÿä ÿ™ÿ≠ÿ™-ÿØŸàŸÖŸäŸÜ ŸÖŸÜŸá */
function hostMatches(host: string, domain: string) {
  return host === domain || host.endsWith(`.${domain}`);
}

/** ŸÑÿßÿ¶ÿ≠ÿ© ÿßŸÑÿØŸàŸÖŸäŸÜÿßÿ™ ÿßŸÑÿ™Ÿä Ÿäÿ¨ÿ® ÿ™ÿ≠ŸÖŸäŸÑŸáÿß ŸÖÿ®ÿßÿ¥ÿ±ÿ© (ÿ®ÿØŸàŸÜ ÿ®ÿ±ŸàŸÉÿ≥Ÿä) */
const SKIP_DIRECT_HOSTS = [
  // Contentful
  'images.ctfassets.net',
  'assets.ctfassets.net',
  'downloads.ctfassets.net',
  'videos.ctfassets.net',

  // Emaar
  'cdn.properties.emaar.com',
  'properties.emaar.com',

  // Sobha
  'sobharealty.com',
  'sobha.com',

  // Nakheel
  'www.nakheel.com',
  'nakheel-aut.sitefinity.cloud',

  // DAMAC CDN
  'prod-cdn.damacproperties.com',

  // PropVR / Matterport (ÿ¨ŸàŸÑÿßÿ™ 3D)
  'view.propvr.tech',
  'propvr.tech',
  'my.matterport.com',
  'matterport.com',

  // ŸÖŸÜÿµÿßÿ™ ŸÅŸäÿØŸäŸà
  'www.youtube.com',
  'youtu.be',
  'player.vimeo.com',

  // ÿÆÿ±ÿßÿ¶ÿ∑
  'www.google.com',
  'maps.google.com',

  // ŸÖÿ≥ÿ™ÿ∂ŸäŸÅÿßÿ™ Ÿàÿ´ÿßÿ¶ŸÇ
  'dubaiholding.box.com',
  'www.scribd.com',
];

/**
 * ‚úÖ ÿßŸÑÿØÿßŸÑÿ© ÿßŸÑŸÖŸàÿ≠ŸëÿØÿ© ŸÑÿßÿ≥ÿ™ÿÆÿØÿßŸÖŸáÿß ŸÅŸä ÿßŸÑŸÖŸÉŸàŸÜÿßÿ™:
 * - ÿ™ÿπŸäÿØ ÿ±ÿßÿ®ÿ∑Ÿãÿß ŸÖÿ®ÿßÿ¥ÿ±Ÿãÿß ŸÑŸÑÿØŸàŸÖŸäŸÜÿßÿ™ ÿßŸÑŸÖŸàÿ´ŸàŸÇÿ© ÿ£ÿπŸÑÿßŸá
 * - ÿ™ŸÖÿ±Ÿëÿ± ÿßŸÑÿ®ÿßŸÇŸä ÿπÿ®ÿ± /api/proxy/file
 * - ÿ™ÿ≠ÿßŸÅÿ∏ ÿπŸÑŸâ ÿßŸÑÿ±Ÿàÿßÿ®ÿ∑ ÿßŸÑŸÖÿ≠ŸÑŸäÿ©/ÿßŸÑŸÜÿ≥ÿ®Ÿäÿ© Ÿà data:/blob:
 */
export function directAccess(src: string | null | undefined): string {
  if (!src) return '';

  const raw = src.trim();
  const lower = raw.toLowerCase();

  // ÿßÿ™ÿ±ŸÉ ÿßŸÑÿ±Ÿàÿßÿ®ÿ∑ ÿßŸÑŸÖÿ≠ŸÑŸäÿ© ŸàÿßŸÑŸÄ data/blob ŸÉŸÖÿß ŸáŸä
  if (
    lower.startsWith('/') ||                // ÿØÿßÿÆŸÑ public ÿ£Ÿà ŸÖÿ≥ÿßÿ± ŸÜÿ≥ÿ®Ÿä
    lower.startsWith('data:') ||
    lower.startsWith('blob:')
  ) {
    return raw;
  }

  // ÿßÿ®ŸÜŸê URL ÿ®ŸÇÿßÿπÿØÿ© ÿ¢ŸÖŸÜÿ© ŸÅŸä SSR/CSR
  const base =
    typeof window !== 'undefined' && (window as any).location?.origin
      ? (window as any).location.origin
      : 'http://localhost';

  let u: URL;
  try {
    u = new URL(raw, base);
  } catch {
    // ŸÅŸä ÿ≠ÿßŸÑ ŸÖÿ≥ÿßÿ± ÿ∫Ÿäÿ± ŸÇÿßÿ®ŸÑ ŸÑŸÑÿ®ÿßÿ±ÿ≥ÿå ÿ£ÿπÿØŸá ŸÉŸÖÿß ŸáŸà
    return raw;
  }

  const host = (u.hostname || '').toLowerCase();

  // ÿ•ŸÜ ŸÉÿßŸÜ ÿ∂ŸÖŸÜ ŸÑÿßÿ¶ÿ≠ÿ© ÿßŸÑÿ™ÿÆÿ∑Ÿäÿå ÿ≠ŸÖŸëŸÑ ŸÖÿ®ÿßÿ¥ÿ±ÿ©
  if (SKIP_DIRECT_HOSTS.some((d) => hostMatches(host, d))) {
    return u.toString();
  }

  // ÿÆŸÑÿßŸÅ ÿ∞ŸÑŸÉÿå ŸÖÿ±Ÿëÿ±Ÿá ÿπÿ®ÿ± ÿßŸÑÿ®ÿ±ŸàŸÉÿ≥Ÿä
  return `/api/proxy/file?url=${encodeURIComponent(u.toString())}`;
}